<template>
  <div>
    <button @click="inCounter">Add 1</button>
    <button @click="outCounter">minus 1</button>
    <p>{{ counter }}</p>
  </div>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      counter: 0
    }
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {
    inCounter() {
      this.counter += 1
    },
    outCounter() {
      this.counter -= 1
    }
  }
}
</script>

<style scoped>
button {
  margin: 10px;
}
</style>
